<template>
  <div class="head">
    <span class="title"></span>
    <br />
    <br />
    <el-form
      :model="celebrityForm"
      :rules="rules"
      ref="celebrityForm"
      label-width="100px"
      class="celebrity-form"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="celebrityForm.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="gender">
        <el-select v-model="celebrityForm.gender" placeholder="">
          <el-option label="" value="male"></el-option>
          <el-option label="" value="female"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="ID" prop="douban_id">
        <el-input v-model="celebrityForm.douban_id"></el-input>
      </el-form-item>
      <el-form-item label="" prop="nama_en">
        <el-input v-model="celebrityForm.nama_en"></el-input>
      </el-form-item>
      <el-form-item label="" prop="born_place">
        <el-input v-model="celebrityForm.born_place"></el-input>
      </el-form-item>
      <el-form-item label="" prop="aka">
        <el-input v-model="celebrityForm.aka" placeholder="'/'"></el-input>
      </el-form-item>
      <el-form-item label="" prop="aka_en">
        <el-input v-model="celebrityForm.aka_en" placeholder="'/'"></el-input>
      </el-form-item>
      <el-form-item label="" prop="avatar">
        <el-upload
          class="upload-demo"
          ref="upload"
          action
          :file-list="celebrityForm.avatarFile"
          :auto-upload="false"
          :on-change="handleChange"
          :show-file-list="false"
        >
          <el-button slot="trigger" size="small" type="primary"></el-button>
          <div slot="tip" class="el-upload__tip">jpg/png2Mb</div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('celebrityForm')" v-loading="loading"></el-button>
        <el-button @click="resetForm('celebrityForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      celebrityForm: {
        name: "",
        gender: "",
        douban_id: "",
        name_en: "",
        born_place: "",
        aka: "",
        aka_en: "",
        avatar: []
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        gender: [
          { required: true, message: "", trigger: "change" }
        ]
        // avatar: [{ required: true, message: "", trigger: "blur" }]
      },
      loading: false
    };
  },
  created() {
    this.fetchCelebrityInfo();
  },
  methods: {
    handleChange(file, fileList) {
      const isLt2M = file.size / 1024 / 1024 < 2;
      const isJPG =
        file.raw.type === "image/jpeg" || file.raw.type === "image/png";
      if (!isLt2M) {
        this.$message.error(" 2MB!");
        return false;
      }
      if (!isJPG) {
        this.$message.error(" JPG/PNG !");
        return false;
      }
      this.$message.success("");
      this.celebrityForm.avatar = file;
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (!this.celebrityForm.avatar) {
            this.$message.error("");
            return false;
          }
          this.updateCelebrity();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    fetchCelebrityInfo() {
      var url = "/celebrity/" + this.$route.params.celebrityid;
      this.$http
        .get(url)
        .then(response => {
          var info = response.data.data;
          console.log(info);
          this.celebrityForm.name = info.name;
          if (info.gender === 0) {
            this.celebrityForm.gender = "male";
          } else {
            this.celebrityForm.gender = "female";
          }
          this.celebrityForm.name_en = info.name;
          this.celebrityForm.born_place = info.born_place;
          if (info.aka_list) {
            for (var i = 0; i < info.aka_list.length; i++) {
              this.celebrityForm.aka += info.aka_list[i] + "/";
            }
          }
          if (info.aka_en_list) {
            for (var i = 0; i < info.aka_en_list.length; i++) {
              this.celebrityForm.aka_en += info.aka_en_list[i] + "/";
            }
          }

          this.celebrityForm.douban_id = info.douban_id;

          this.loading = false;
        })
        .catch(error => {
          this.$message.error("");
          this.$router.push("/");
          console.log("fetch celebrity info error.");
          this.loading = false;
        });
    },
    updateCelebrity() {
      this.loading = true;
      var params = new FormData();
      params.append("image", this.celebrityForm.avatar.raw);
      params.append("name", this.celebrityForm.name);
      params.append("douban_id", this.celebrityForm.douban_id);
      params.append("gender", this.celebrityForm.gender);
      params.append("name_en", this.celebrityForm.name_en);
      params.append("born_place", this.celebrityForm.born_place);
      params.append("aka", this.celebrityForm.aka);
      params.append("aka_en", this.celebrityForm.aka_en);
      this.$http
        .patch("/celebrity/" + this.$route.params.celebrityid, params)
        .then(response => {
          this.loading = false;
          this.$message.success("");
        })
        .catch(error => {
          this.loading = false;
          this.$message.error(",");
        });
    }
  }
};
</script>

<style lang="less">
.celebrity-form {
  max-width: 500px;
}
</style>
